.photo-editor__form {
  .wrapper();

  padding-top: 20px;

  background-color: @bg-grey;
}

.photo-editor__canvas {
  img {
    display: block;
  }
}

.photo_editor__control-btn {
  display: none;
}

.photo-editor__tools-list {
  display: flex;
  align-items: center;
  position: relative;

  .list-reset();

  padding-top: 40px;
  padding-left: 34px;
  padding-bottom: 71px;
}

.photo-editor__tools-item {
  // position: relative;

  display: block;
  width: 50px;
  height: 50px;
  margin-right: 30px;

  &--crop {
    svg {
      width: 48px;
    }
  }

  &--fill {
    padding-top: 2px;

    svg {
      width: 48px;
    }
  }

  &--contrast {
    padding-top: 8px;
    padding-left: 8px;

    svg {
      width: 32px;
    }
  }
}

.photo-editor__tool {
  cursor: pointer;

  fill: #b5b9be;

  &:hover {
    fill: #8d949b;
  }

  svg {
    /* Если не выставить едут иконки
       в планшетной версии
    */
    height: auto;
  }
}

.photo_editor__control-btn--crop:checked ~ .photo-editor__tools-list .photo-editor__tools-item--crop svg,
.photo_editor__control-btn--fill:checked ~ .photo-editor__tools-list .photo-editor__tools-item--fill svg,
.photo_editor__control-btn--contrast:checked ~ .photo-editor__tools-list .photo-editor__tools-item--contrast svg {
  fill: @cl-pink;
}

.photo_editor__control-btn--crop:checked ~ .photo-editor__tools-list .photo-editor__tools-item--crop .photo-editor__slider,
.photo_editor__control-btn--fill:checked ~ .photo-editor__tools-list .photo-editor__tools-item--fill .photo-editor__slider,
.photo_editor__control-btn--contrast:checked ~ .photo-editor__tools-list .photo-editor__tools-item--contrast .photo-editor__slider {
  display: block;
}

.photo-editor__slider {
  position: absolute;
  display: none;
  left: 18px;
  bottom: 29px;

  width: 240px;
}

.photo-editor__buttons {
  margin: 0 -20px;
  padding: 0 20px;
  padding-top: 38px;
  padding-bottom: 27px;

  border-top: 1px solid #dddfe0;
}

.photo-editor__button {
  display: block;
  width: 280px;
  padding-top: 15px;
  padding-bottom: 16px;
  margin-bottom: 13px;

  font-size: 18px;
  font-weight: bold;
  line-height: 18px;

  border: 3px solid;
  border-radius: 27px;
  cursor: pointer;

  &:focus {
    outline: none;
  }

  &--post {
    color: @cl-pink;

    &:hover {
      color: #ffffff;
      background-color: @cl-pink;
      border-color: @cl-pink;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
      background-color: @cl-pink;
      border-color: @cl-pink;
      outline: none;
    }
  }

  &--cancel {
    color: rgba(40, 54, 69, 0.1);
    border-color: #dddfe0;

    &:hover {
      color: rgba(40, 54, 69, 0.3);
      border-color: #b5b9be;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
      border-color: #b5b9be;
      background-color: #b5b9be;
      outline: none;
    }
  }
}

@media (min-width: @tablet-width) {
  .photo-editor__form {
    display: flex;

    width: 660px;
    padding-top: 57px;
    padding-bottom: 30px;
  }

  .photo-editor__canvas {
    margin-right: 20px;
  }

  .photo-editor__controls {
    display: flex;
    flex-wrap: wrap;

    width: 300px;
    padding-left: 28px;
  }

  .photo-editor__tools-list {
    flex-direction: column;
    justify-content: flex-start;

    width: 100%;
    padding-left: 0;
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 67px;
  }

  .photo-editor__tools-item {
    position: relative;

    width: 100%;
    height: auto;
    margin-right: 0;
    padding-right: 18px;

    border-bottom: 1px solid #dddfe0;

    &--crop {
      padding-top: 8px;
      padding-bottom: 16px;

      svg {
        width: 24px;
      }
    }

    &--fill {
      padding-top: 27px;
      padding-bottom: 14px;

      svg {
        width: 24px;
      }
    }

    &--contrast {
      padding-top: 25px;
      padding-left: 5px;
      padding-bottom: 20px;

      svg {
        width: 16px;
      }
    }
  }

  .photo-editor__tool {
    display: block;

    svg {
      fill: @cl-pink;
    }
  }

  .photo-editor__slider {
    left: auto;
    right: 18px;
    bottom: 28px;

    display: block;
    width: 202px;
  }

  .photo-editor__buttons {
    border: none;
    width: 100%;
    padding: 0;
    margin: 0;
    padding-right: 13px;
  }

  .photo-editor__button {
    width: 100%;
    margin-bottom: 24px;
    padding-left: 0;
  }
}

@media (min-width: @desktop-width) {
  .photo-editor {
    margin-bottom: 27px;
  }

  .photo-editor__wrapper {
    .wrapper(960px, 10px);
  }

  .photo-editor__form {
    width: 100%;
    padding: 0;
    margin: 0;
    padding-top: 42px;
    padding-right: 60px;
    padding-bottom: 57px;

    justify-content: space-between;
  }

  .photo-editor__canvas {
    display: flex;

    width: 300px;
    margin-left: 80px;
    margin-top: 15px;
  }

  .photo-editor__controls {
    width: 410px;
    padding-left: 0;
  }

  .photo-editor__tools-item {
    padding-right: 30px;

    &--crop {
      padding-top: 11px;
      padding-bottom: 4px;

      svg {
        width: 48px;
      }
    }

    &--fill {
      padding-top: 15px;
      padding-bottom: 2px;

      svg {
        width: 48px;
      }
    }

    &--contrast {
      padding-top: 18px;
      padding-left: 6px;
      padding-bottom: 12px;

      svg {
        width: 31.92px;
      }
    }
  }

  .photo-editor__tool {
    .photo-editor__tools-item--crop & {
      margin-left: -2px;
    }
  }

  .photo-editor__slider {
    right: 30px;

    width: 288px;
  }

  .photo-editor__buttons {
    display: flex;
    justify-content: space-between;

    margin-top: auto;
    padding-right: 0;
  }

  .photo-editor__button {
    width: 184px;
    height: 56px;
    margin-bottom: 0;
    padding-left: 10px;
  }
}
