.download {
  margin-top: -66px;

  background: @bg-shop;
  background-image: url("../img/iphone-hand-mobile@1x.png"),
    url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: left calc(50% - 31px) bottom 272px,
    bottom 272px center;

  @media (min-resolution: @retina-dpi),
    (min-resolution: @retina-dppx) {
    background-image: url("../img/iphone-hand-mobile@2x.png"),
      url("../img/bg-back-mobile@2x.jpg");
    background-size: 258px auto,
      320px auto;
  }
}

.download__wrapper {
  .wrapper();

  padding-bottom: 31px;
}

.download__picture {
  display: flex;
  align-items: flex-end;

  height: 396px;
}

.download__shop {
  display: flex;
  flex-direction: column;
}

.download__slogan {
  display: none;

  font-size: 40px;
  font-weight: 300;
  color: #ffffff;
}

@media (min-width: @tablet-width) {
  .download {
    margin-top: -110px;
    //min-height: 730px;

    background-image: url("../img/iphone-hand-tablet@1x.png"),
      url("../img/bg-back-tablet@1x.jpg");
    background-position: bottom;
    /*background-size: cover;*/

    @media (min-resolution: @retina-dpi),
      (min-resolution: @retina-dppx) {
      background-image: url("../img/iphone-hand-tablet@2x.png"),
        url("../img/bg-back-tablet@2x.jpg");
      background-size: 700px auto, 1200px auto;
    }
  }

  .download__wrapper {
    width: 660px;
    padding-top: 202px;
    padding-bottom: 110px;
  }

  .download__picture {
    display: none;
  }

  .download__shop {
    display: flex;
    flex-direction: column;

    width: 300px;
  }

  .download__slogan {
    display: block;

    margin: 0;
    margin-bottom: 30px;
    padding: 0 55px;

    line-height: 48px;
    text-align: center;
  }
}

@media (min-width: @desktop-width) {
  .download {
    background-image: url("../img/iphone-hand-desktop@1x.png"),
      url("../img/bg-back-desktop@1x.jpg");
    background-size: 1050px auto, cover;
    background-position: left calc(50% + 71px) bottom,
      bottom center;

    @media (min-resolution: @retina-dpi),
      (min-resolution: @retina-dppx) {
      background-image: url("../img/iphone-hand-desktop@2x.png"),
        url("../img/bg-back-desktop@2x.jpg");
    }
  }

  .download__wrapper {
    .wrapper(960px, 10px);
    padding-top: 218px;
    padding-bottom: 122px;
  }

  .download__slogan {
    margin-bottom: 51px;
    padding: 0 24px;

    font-size: 50px;
    line-height: 60px;
  }
}
